import { getToken } from '@/api/old/qiniu'; // 获取七牛token 后端通过Access Key,Secret Key,bucket等生成token
// 七牛官方sdk https://developer.qiniu.com/sdk#official-sdk

export default {
  data: function data() {
    return {
      dataObj: {
        token: '',
        key: ''
      },
      image_uri: [],
      fileList: []
    };
  },
  methods: {
    beforeUpload: function beforeUpload() {
      var that = this;
      return new Promise(function (resolve, reject) {
        getToken().then(function (res) {
          var key = res.data.qiniu_key;
          var token = res.data.qiniu_token;
          /* eslint no-underscore-dangle: 0 */

          that._data.dataObj.token = token;
          that._data.dataObj.key = key;
          resolve(true);
        }).catch(function (err) {
          console.log(err);
          /* eslint prefer-promise-reject-errors: 0 */

          reject(false);
        });
      });
    }
  }
};